#contact {
  background: #1b242f;
  color: #eee;
}

#contact .title-line {
  border-color: #eee;
}

.contact-info {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
}

.contact-info div {
  display: flex;
  flex-direction: column;
  min-width: 25%;
  margin-top: 25px;
}

.contact-info i {
  margin-bottom: 10px;
}

.contact-info a {
  text-decoration: none;
  color: #eee;
}

.contact-info a:hover {
  color: #777;
}