.bar {
  height: 30px;
  background: #ddd;
  z-index: 1;
  margin-bottom: 20px;
  width: 90%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.bar-title {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 20%;
  background: #cd661b;
  color: #eee;
  font-weight: bold;
  font-size: 0.8em;
}

.fill-container {
  width: 70%;
  height: 100%;
}

.percentage {
  width: 10%;
}

@media only screen and (max-width: 900px) {
  .percentage, .bar-title {
    font-size: 10px;
  }
}