* {
  font-family: 'Raleway';
  text-align: center;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

button {
  outline: none;
}

.section {
  padding: 12vh 5vw;
  display: flex;
  align-items: center;
  flex-direction: column;
  color: #444;
}

.section h1 {
  font-size: 2.8rem;
  text-transform: uppercase;
  font-weight: normal;
}

.title-line {
  margin: 20px 0 50px 0;
  border: 2px solid #444;
  border-radius: 1px;
  padding: 0 2.5rem;
}

.hover-trans {
  transition: all 0.4s;
}