nav {
  width: 100%;
  background: #1b242f;
  text-transform: uppercase;
  padding: 0 25px;
  position: fixed;
  z-index: 1000;
  border-bottom: 1px solid rgba(0,0,0,0.5);
}

.nav-links {
  padding: 0 25px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.nav-links span {
  padding: 15px 20px;
}

.nav-links span a {
  text-decoration: none;
  color: #eee;
  font-size: 1.05rem;
}

.nav-links span a:hover {
  color: #cd661b;
}

.nav-button {
  display: none;
}

@media screen and (max-width: 800px) {
  nav {
    padding: 10px 50px;
    text-align: right;
  }

  .nav-button {
    display: inline-block;
  }

  .nav-button i {
    color: #eee;
    cursor: pointer;
  }

  .nav-links {
    visibility: hidden;
    display: inline-block;
    position: absolute;
    right: 5px;
    top: -100px;
    opacity: 0;
    min-width: 20%;
    background: #1b242f;
    border: 1px solid #eee;
    transition: all .7s;
  }
  
  .nav-links span {
    display: block;
  }

  .nav-display {
    top: 53px;
    visibility: visible;
    opacity: 1;
  }
}