.project {
  width: 100%;
}

.project-cover {
  min-height: 300px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.project-info, .learn-btn {
  visibility: hidden;
}

.project-cover:hover {
  background: #eee;
}

.project-cover:hover .project-info,
.project-cover:hover .learn-btn {
  visibility: visible;
}

.project-info {
  padding-bottom: 70px;
}

.project-info h3 {
  font-size: 1.4em;
}

.project-info h5 {
  font-weight: normal;
  font-size: 1.1em;
  color: #cd661b;
  padding-top: 5px;
}

.learn-btn {
  background: transparent;
  border: 2px solid #cd661b;
  padding: 15px;
  text-transform: uppercase;
  font-size: 1.2em;
}

.project-info h3, .learn-btn {
  color: #1b242f;
}

.learn-btn:hover {
  background: #cd661b;
  color: #eee;
  cursor: pointer;
}

.modal {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.modal > * {
  margin-bottom: 15px;
}

.about-project {
  width: 60%;
  line-height: 1.4em;
  font-weight: normal;
}

.project-links {
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.project-links a {
  text-decoration: none;
  margin-top: 10px;
  padding: 15px;
  border: 3px solid #1b242f;
  border-radius: 3px;
  font-weight: bold;
  color: black;
}

.project-links a:hover {
  background: #1b242f;
  color: white;
}