

#home {
  height: 100vh;
  background: #1b242f;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

#home h1 {
  margin: 10px;
  font-weight: normal;
  font-size: 2.2em;
}

#home h1 span {
  color: #cd661b;
}

#home button {
  margin-top: 20px;
  background: transparent;
  border: 3px solid #eee;
  border-radius: 3px;
  padding: 15px;
  font-size: 23px;
  color: #eee;
  cursor: pointer;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
  outline: none;
}

@media screen and (max-width: 600px) {
  #home button {
    padding: 8px;
    font-size: 18px;
    border: 2px solid #eee;
  }
}

#home button:hover {
  background: #cd661b;
  border-color: #cd661b;
}

#home button i {
  margin-left: 5px;
  -webkit-transition: -webkit-transform 0.4s;
  transition: -webkit-transform 0.4s;
  transition: transform 0.4s;
  transition: transform 0.4s, -webkit-transform 0.4s; 
}

#home button:hover i {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
nav {
  width: 100%;
  background: #1b242f;
  text-transform: uppercase;
  padding: 0 25px;
  position: fixed;
  z-index: 1000;
  border-bottom: 1px solid rgba(0,0,0,0.5);
}

.nav-links {
  padding: 0 25px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.nav-links span {
  padding: 15px 20px;
}

.nav-links span a {
  text-decoration: none;
  color: #eee;
  font-size: 1.05rem;
}

.nav-links span a:hover {
  color: #cd661b;
}

.nav-button {
  display: none;
}

@media screen and (max-width: 800px) {
  nav {
    padding: 10px 50px;
    text-align: right;
  }

  .nav-button {
    display: inline-block;
  }

  .nav-button i {
    color: #eee;
    cursor: pointer;
  }

  .nav-links {
    visibility: hidden;
    display: inline-block;
    position: absolute;
    right: 5px;
    top: -100px;
    opacity: 0;
    min-width: 20%;
    background: #1b242f;
    border: 1px solid #eee;
    -webkit-transition: all .7s;
    transition: all .7s;
  }
  
  .nav-links span {
    display: block;
  }

  .nav-display {
    top: 53px;
    visibility: visible;
    opacity: 1;
  }
}
.bar {
  height: 30px;
  background: #ddd;
  z-index: 1;
  margin-bottom: 20px;
  width: 90%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.bar-title {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 20%;
  background: #cd661b;
  color: #eee;
  font-weight: bold;
  font-size: 0.8em;
}

.fill-container {
  width: 70%;
  height: 100%;
}

.percentage {
  width: 10%;
}

@media only screen and (max-width: 900px) {
  .percentage, .bar-title {
    font-size: 10px;
  }
}
.bar-display {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

@media only screen and (max-width: 900px) {
  .bar-display {
    width: 100%;
  }
}
#about {
  color: #444;
  background: #eee;
  min-height: 100vh;
}

.about-info {
  width: 100%;
  margin: 40px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.personal {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.personal img {
  max-width: 350px;
  width: 50%;
  margin-bottom: 25px;
}

.personal h3 {
  font-size: 1.6em;
  margin-bottom: 15px;
}

.personal p {
  width: 80%;
  font-size: 1.1em;
  line-height: 1.5em;
}

@media only screen and (max-width: 900px) {
  .about-info {
    flex-direction: column;
  }
  .personal {
    width: 100%;
    margin-bottom: 40px;
  }
}
#resume {
  background: #1b242f;
  color: #eee;
  min-height: 400px;
}

#resume .title-line {
  border-color: #eee;
}

#resume h3 {
  margin-top: 15px;
  font-size: 1.5em;
}

#resume a {
  margin-top: 30px;
  padding: 20px;
  border: 3px solid #eee;
  border-radius: 3px;
  background: transparent;
  font-size: 20px;
  color: #eee;
  text-decoration: none;
  outline: none;
  cursor: pointer;
}

#resume a:hover {
  background: #cd661b;
  border-color: #cd661b;
}
.project {
  width: 100%;
}

.project-cover {
  min-height: 300px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.project-info, .learn-btn {
  visibility: hidden;
}

.project-cover:hover {
  background: #eee;
}

.project-cover:hover .project-info,
.project-cover:hover .learn-btn {
  visibility: visible;
}

.project-info {
  padding-bottom: 70px;
}

.project-info h3 {
  font-size: 1.4em;
}

.project-info h5 {
  font-weight: normal;
  font-size: 1.1em;
  color: #cd661b;
  padding-top: 5px;
}

.learn-btn {
  background: transparent;
  border: 2px solid #cd661b;
  padding: 15px;
  text-transform: uppercase;
  font-size: 1.2em;
}

.project-info h3, .learn-btn {
  color: #1b242f;
}

.learn-btn:hover {
  background: #cd661b;
  color: #eee;
  cursor: pointer;
}

.modal {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.modal > * {
  margin-bottom: 15px;
}

.about-project {
  width: 60%;
  line-height: 1.4em;
  font-weight: normal;
}

.project-links {
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.project-links a {
  text-decoration: none;
  margin-top: 10px;
  padding: 15px;
  border: 3px solid #1b242f;
  border-radius: 3px;
  font-weight: bold;
  color: black;
}

.project-links a:hover {
  background: #1b242f;
  color: white;
}
.projects {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.proj {
  width: 33%;
}

@media screen and (max-width: 960px) {
  .proj {
    width: 50%;
  }
}

@media screen and (max-width: 600px) {
  .proj {
    width: 100%;
    margin-bottom: 10px;
  }
}

.project-on-mobile {
  display: none;
}

.project-on-mobile .about-project {
  color: #444;
  width: 100%;
  padding: 10px;
  line-height: 1.6em;
}

@media screen and (max-width: 600px) {
  .project-on-mobile {
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
    padding: 10px 0 30px;
    border-bottom: 2px solid #444;
  }
}
#portfolio {
  color: #444;
  background: #eee;
  min-height: 100vh;
}
#contact {
  background: #1b242f;
  color: #eee;
}

#contact .title-line {
  border-color: #eee;
}

.contact-info {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
}

.contact-info div {
  display: flex;
  flex-direction: column;
  min-width: 25%;
  margin-top: 25px;
}

.contact-info i {
  margin-bottom: 10px;
}

.contact-info a {
  text-decoration: none;
  color: #eee;
}

.contact-info a:hover {
  color: #777;
}
* {
  font-family: 'Raleway';
  text-align: center;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

button {
  outline: none;
}

.section {
  padding: 12vh 5vw;
  display: flex;
  align-items: center;
  flex-direction: column;
  color: #444;
}

.section h1 {
  font-size: 2.8rem;
  text-transform: uppercase;
  font-weight: normal;
}

.title-line {
  margin: 20px 0 50px 0;
  border: 2px solid #444;
  border-radius: 1px;
  padding: 0 2.5rem;
}

.hover-trans {
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
}
