.projects {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.proj {
  width: 33%;
}

@media screen and (max-width: 960px) {
  .proj {
    width: 50%;
  }
}

@media screen and (max-width: 600px) {
  .proj {
    width: 100%;
    margin-bottom: 10px;
  }
}

.project-on-mobile {
  display: none;
}

.project-on-mobile .about-project {
  color: #444;
  width: 100%;
  padding: 10px;
  line-height: 1.6em;
}

@media screen and (max-width: 600px) {
  .project-on-mobile {
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
    padding: 10px 0 30px;
    border-bottom: 2px solid #444;
  }
}